import {
  App,
  BitmapConfigParser,
  EngineEvent,
  EngineObject,
  Events,
  Light,
  ModelObject3d,
  Object3d,
  ObjectManager,
  Plane3d,
  PlaneType,
  Rect3d,
  Rect2d,
  RenderType,
  ResourceResolver,
  Colour
} from "synaren-engine";
import Cube from "./Cube";
import Ground3d from "./Ground3d";
import Sphere from "./Sphere";


export default class World extends ObjectManager {
  fontCacheId: number;
  bitmapLoader: BitmapConfigParser;
  ground: Ground3d;
  racingCar: ModelObject3d;
  lowPolyGirl: ModelObject3d;
  cube: Cube;
  cube2: Cube;
  sun: Sphere;
  road: Plane3d;
  lowPolyTree: ModelObject3d[];
  sky: Sphere;

  constructor() {
    super();
  }

  render() {
    // console.log('=== World render() called with entities:', this.entities.length, '===');
    this.entities.forEach((ent: EngineObject, index: number) => {
      // if (ent) {
      //   console.log(`Rendering entity ${index}:`, ent.constructor.name, 'at position:', ent.position);
      //   console.log(`Entity ${index} shaderEntity:`, ent.shaderEntity ? 'exists' : 'null');
      //   console.log(`Entity ${index} hidden:`, ent.hidden);
      //   try {
          ent.render(this.engineHelper);
    //       console.log(`Entity` ${index} rendered successfully`);
    //     } catch (error) {
    //       console.error(`Error rendering entity ${index}:`, error);
    //     }
    //   } else {
    //     console.log(`Entity ${index} is null/undefined`);
    //   }
    });
    // console.log('=== End World` render() ===');
  }

  update() {
    this.entities.forEach((ent: EngineObject) => {
      if (ent) ent.update(this.engineHelper);
    });
    const { x, y, z } = this.engineHelper.camera.camera3d.position;
    if (this.sky) this.sky.center(x, y, z);
  }

  event(event: EngineEvent) {
    super.event(event);
    if (event.eventType === Events.DRAG) {
      console.log('Camera drag event:', { dx: -event.dxRaw, dy: event.dyRaw });
      this.engineHelper.camera.camera3d.angleY(-event.dxRaw);
      this.engineHelper.camera.camera3d.angleX(event.dyRaw);
      this.engineHelper.camera.camera3d.updateProjectionView();
      console.log('Camera position after drag:', this.engineHelper.camera.camera3d.position);
    }
    return undefined;
  }

  loadResources() {
    console.log('Loading resources...');
    
    this.engineHelper
      .getResource("assets/sphere.txt")
      .then(
        ResourceResolver.objResolverMultiple(this.engineHelper, [
          { textureSource: "assets/background.jpg", name: "background" },
          { textureSource: "assets/sun.png", name: "sun" }
        ])
      ).then(() => console.log('Sphere resources loaded'))
      .catch(err => console.error('Sphere resource error:', err));

    this.engineHelper
      .getResource("assets/low_poly_tree.txt")
      .then(
        ResourceResolver.objResolver(
          this.engineHelper,
          "assets/low_poly_tree.png",
          "low_poly_tree"
        )
      ).then(() => console.log('Tree resources loaded'))
      .catch(err => console.error('Tree resource error:', err));

    this.engineHelper
      .getResource("assets/low_poly_girl.txt")
      .then(
        ResourceResolver.objResolver(
          this.engineHelper,
          "assets/low_poly_girl.png",
          "low_poly_girl"
        )
      ).then(() => console.log('Girl resources loaded'))
      .catch(err => console.error('Girl resource error:', err));

    this.engineHelper
      .getResource("assets/racing_car.txt")
      .then(
        ResourceResolver.objResolver(
          this.engineHelper,
          "assets/racing_car.png",
          "racing_car"
        )
      ).then(() => console.log('Car resources loaded'))
      .catch(err => console.error('Car resource error:', err));

    this.engineHelper
      .getResource("assets/paprika.fnt")
      .then(ResourceResolver.bmFontResolver(this.engineHelper))
      .then(() => console.log('Font resources loaded'))
      .catch(err => console.error('Font resource error:', err));

    this.engineHelper
      .getResource("assets/atlas.txt")
      .then(
        ResourceResolver.bitmapResolver(this.engineHelper, 1024, 1024, 20e-3)
      ).then(() => console.log('Atlas resources loaded'))
      .catch(err => console.error('Atlas resource error:', err));

    this.engineHelper
      .getResource(`/assets/missing-texture.txt`)
      .then(ResourceResolver.bitmapResolver(this.engineHelper, 1184, 1184, 0))
      .then(() => console.log('Missing texture resources loaded'))
      .catch(err => console.error('Missing texture resource error:', err));
  }

  init() {
    console.log('World init() called');
    // initialise all the engine objects first
    super.init();
    
    // Set camera position and look at scene center for perspective
    this.engineHelper.camera.camera3d.center(0, 2, 0); // Camera much further back to see the scene properly
    this.engineHelper.camera.camera3d.angleX(0); // Level view initially
    this.engineHelper.camera.camera3d.angleY(0); // Face forward initially
    this.engineHelper.camera.camera3d.angleZ(0);
    this.engineHelper.camera.camera3d.setZoom({ x: 1, y: 1, z: 1 }); // Normal scale - NOT zero!
    this.engineHelper.camera.camera3d.updateProjectionView();

    console.log('Camera positioned at:', this.engineHelper.camera.camera3d.position);
    console.log('Camera view matrix:', this.engineHelper.camera.camera3d.viewMatrix);
    console.log('Camera frustum matrix:', this.engineHelper.camera.camera3d.frustum);
    console.log('Camera rotation:', { 
      ax: this.engineHelper.camera.camera3d.position.ax, 
      ay: this.engineHelper.camera.camera3d.position.ay, 
      az: this.engineHelper.camera.camera3d.position.az 
    });

    // Set up lighting similar to gabbos-guild
    this.engineHelper.setLighting(
      new Light({
        pos: [-20, 15, -20],
        in: [0.5, 0.5, 0.5], // Increased brightness
        attenuation: 0.015,
        ambientCoeff: 0.3,
        at: [0, 0, 0],
      })
    );

    console.log('Lighting set up');


    // Sky (Sphere) - properly sized background sphere that follows camera
    this.sky = new Sphere(
      new Rect3d(0.0, 4.0, 20.0, 100.0, 100.0, 100.0), // Much larger sphere, initially centered on camera
      "background"
    );
    this.addEntity(this.sky);
    console.log('Sky added');

    // Low poly girl - positioned to the right (Z axis), on ground
    this.lowPolyGirl = new Object3d(
      new Rect3d(0.0, 0.0, 3.0, 1.0, 1.0, 1.0), // X=0, Y=0 (ground), Z=3 (right)
      "low_poly_girl"
    );
    this.lowPolyGirl.angleY(90); // Face towards center (along -Z)
    this.addEntity(this.lowPolyGirl);
    console.log('Low poly girl added at:', this.lowPolyGirl.position);

    // Racing car - positioned to the left (Z axis), on ground
    this.racingCar = new Object3d(
      new Rect3d(0.0, 0.0, -3.0, 1.0, 1.0, 1.0), // X=0, Y=0 (ground), Z=-3 (left)
      "racing_car"
    );
    this.racingCar.angleY(-90); // Face towards center (along +Z)
    this.addEntity(this.racingCar);
    console.log('Racing car added at:', this.racingCar.position);

    // Ground (tiled) - properly sized ground plane, centered at X=0, Y=0, Z=0
    this.ground = new Ground3d(
      new Rect3d(0.0, 0.0, 0.0, 10.0, 0.0, 10.0), // X=0, Y=0 (ground), Z=0
      "ground"
    );
    this.ground.init(this.engineHelper);
    this.addEntity(this.ground);
    console.log('Ground added');

    // Sun (Sphere) - positioned high and to the side
    this.sun = new Sphere(
      new Rect3d(5.0, 8.0, -5.0, 0.8, 0.8, 0.8), // X=5, Y=8 (high), Z=-5
      "sun"
    );
    this.sun.setRenderType(RenderType.TRIANGLE);
    this.sun.init(this.engineHelper);
    this.addEntity(this.sun);
    console.log('Sun added');

    // Low poly trees - create a forest scene around the main objects
    this.lowPolyTree = [];
    const treeCreator = (x: number, z: number, scale: number = 1.0) => {
      const tree = new Object3d(
        new Rect3d(x, 0.0, z, scale, scale, scale), // Y=0 for ground
        "low_poly_tree"
      );
      tree.setRenderType(RenderType.TRIANGLE);
      tree.init(this.engineHelper);
      this.lowPolyTree.push(tree);
      this.addEntity(tree);
    };
    
    // Background trees (along X axis, Z far)
    treeCreator(-8, -4, 1.2);
    treeCreator(-6, -4, 1.0);
    treeCreator(6, -4, 1.1);
    treeCreator(8, -4, 1.3);
    
    // Side trees (along X axis, Z near)
    treeCreator(-8, 4, 0.9);
    treeCreator(-6, 4, 1.1);
    treeCreator(6, 4, 1.0);
    treeCreator(8, 4, 1.2);
    
    // Foreground trees (closer to camera, Z=2)
    treeCreator(-4, 2, 0.8);
    treeCreator(0, 2, 0.7);
    treeCreator(4, 2, 0.9);

    console.log('Trees added, total entities:', this.entities.length);


    console.log('Entity details:');
    this.entities.forEach((entity, index) => {
      console.log(`  Entity ${index}:`, entity.constructor.name, 'position:', entity.position);
    });

    // initialise all the engine objects
    super.init();
    console.log('Super init completed');
    console.log('Final entity count after super.init():', this.entities.length);
  }
}

// Utility to compute frustum params based on aspect ratio
const getFrustum = (aspect) => {
  const frustumHeight = 1;
  const frustumWidth = frustumHeight * aspect;
  return {
    left: -frustumWidth / 2,
    right: frustumWidth / 2,
    bottom: -frustumHeight / 2,
    top: frustumHeight / 2
  };
};

const createApp = () => {
  const aspect = window.innerWidth / window.innerHeight;
  const args = {
    world: new World(),
    elementId: "app",
    canvasId: "app-game",
    error: undefined,
    subscribe: undefined,
    camera: {
      near: 0.01, // Much smaller near plane to avoid clipping close objects
      far: 2.0, // Reasonable far plane
      fov: 120.0, // Use a wider FOV for better view
      maxFov: 120.0, // Set reasonable max FOV
      isFovMax: false, // Don't use max FOV calculation
      projection: 'perspective', // Use perspective projection
      aspect,
      ...getFrustum(aspect),
    },
    aspectRatio: aspect,
    eventThrottle: 1000.0 / 60.0,
    fps: 30,
    isStepRender: false,
  };
  const app = new App(args);
  app.run().catch(console.error);
  app.unpause();
  window.onresize = () => {
    location.reload();
  };
};

window.onload = function () {
  try {
    createApp();
  } catch (error) {
    console.error(error);
  }
};
